import React, {Component} from "react";
import MulticartDocumentLink from "eshop/modules/documents/components/MulticartDocumentLink";
import {Section, Toggle} from 'eshop/modules/core/components/ui/Expander';

class OraDocumentsSectionPart extends Component {
    constructor(props) {
        super(props);
    }

    render() {
        let documents = this.props.documentList.map(document => {
            let key = document.documentCode + (document.bundleNo ? "_" + document.bundleNo : '');
            return <MulticartDocumentLink key={key} {...document} />
        });
        const toggleHeader = <h3 className="h4 opl-section__heading">{this.props.header}</h3>
        const sectionHeader = <div className="l-row" ><Toggle variant="arrow" header={toggleHeader} className="l-col l-col-12 " triggerClassName="opl-document__section__trigger"><span className="opl-section__text-show">rozwiń sekcję Umowy</span><span className="opl-section__text-hide">zwiń sekcję Umowy</span></Toggle></div>

        return (
           <Section header={sectionHeader}
                    id={"document-component-subsection-" + this.props.header}
                    variant="section"
                    className={"opl-document__section" + (this.props.documentList.length === 0 ? ' u-hide' : '')}
                    contentClassName="opl-document__section__content">
               <div className="u-padding-left-l-xl u-small-no-padding-l u-padding-m">
                   <ul>
                       {documents}
                   </ul>
               </div>
           </Section>
       )
    }
}

export default OraDocumentsSectionPart;